.inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inicio__tienda {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 33%;
}
.inicio__usuario {
  display: flex;
  justify-content: space-between;
  width: 75%;
  margin-top: 5rem;
  text-align: center;
}
.inicio__elemento {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
}

.tienda {
  width: 100%;
  margin-top: 90px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  place-content: center;
}
.tienda h2 {
  margin-bottom: 40px;
}
.tienda h3 {
  margin: 0;
}
.tienda h4, .tienda .precio-unidad, .tienda .cabecera-resumen p {
  color: hsl(7, 20%, 60%);
  font-weight: 400;
  margin-bottom: 10px;
}
.tienda p {
  font-weight: 600;
}
.tienda h3, .tienda h4, .tienda p {
  font-size: 16px;
}
.tienda img {
  display: block;
}
.tienda ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
.tienda .productos {
  margin-left: 6.6%;
  margin-right: 6.6%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tienda .contenedor-productos {
  display: grid;
  column-gap: 25px;
}
@media (min-width: 545px) {
  .tienda .contenedor-productos {
    grid-template-columns: repeat(2, 250px);
  }
  .tienda .resumen-orden {
    width: 41%;
  }
}
@media (min-width: 820px) {
  .tienda .contenedor-productos {
    grid-template-columns: repeat(3, 250px);
  }
}
@media (min-width: 1201px) {
  .tienda .contenedor {
    display: flex;
  }
}
.tienda .imagen-producto {
  width: 100%;
  border-radius: 10px;
}
.tienda .boton {
  width: 64%;
  height: 45px;
  margin-top: -22.5px;
  margin-left: 18%;
  margin-right: 18%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  gap: 10px;
}
.tienda .boton p {
  font-size: 12px;
}
.tienda input[type=number]::-webkit-inner-spin-button,
.tienda input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.tienda input[type=number] {
  -moz-appearance: textfield;
}
.tienda .boton input {
  width: 50%;
  text-align: center;
  background-color: hsl(14, 86%, 42%);
  border: none;
  height: 100%;
  color: white;
}
.tienda .boton-cantidad, .tienda .confirmar-orden {
  background-color: hsl(14, 86%, 42%);
  border: 1px solid hsl(14, 86%, 42%);
}
.tienda .boton-cantidad:hover {
  cursor: auto;
}
.tienda .boton-sumar, .tienda .boton-restar {
  background-color: hsl(14, 86%, 42%);
  border: 1px solid white;
  height: 15px;
  width: 15px;
  border-radius: 50px;
  display: grid;
  place-content: center;
}
.tienda .boton-sumar:hover, .tienda .boton-restar:hover {
  background-color: white;
  cursor: pointer;
}
.tienda .boton-sumar:hover img, .tienda .boton-restar:hover img {
  filter: invert(0.4) sepia() hue-rotate(-16deg) saturate(86) brightness(42);
}
.tienda .precio {
  margin-top: 10px;
  margin-bottom: 30px;
  color: hsl(14, 86%, 42%);
}
.tienda .carrito {
  background-color: hsl(20, 50%, 98%);
  margin-left: 2.45%;
  width: 385px;
  height: fit-content;
  border-radius: 10px;
}
.tienda .contenido-carrito, .tienda .carrito-vacio {
  display: grid;
  place-items: center;
}
.tienda .contador-carrito {
  margin-bottom: 30px;
}
.tienda .carrito h2 {
  margin-top: 30px;
  color: hsl(14, 86%, 42%);
  margin-left: 25px;
}
.tienda .texto-carrito-vacio, .tienda .precio-total {
  color: hsl(12, 20%, 44%);
}
.tienda .contenido-carrito {
  margin-left: 6.5%;
  margin-right: 6.5%;
  width: 87%;
}
.tienda .articulo-carrito, .tienda .precio-cantidad, .tienda .ecologico, .tienda .total-orden, .tienda .info {
  display: flex;
}
.tienda .articulo-carrito, .tienda .total-orden {
  justify-content: space-between;
}
.tienda .articulo-carrito {
  border-bottom: 1px solid lightgray;
  align-items: center;
  margin-bottom: 20px;
}
.tienda .precio-cantidad p {
  margin-bottom: 25px;
  margin-top: 15px;
}
.tienda .cantidad {
  margin-right: 15px;
  color: hsl(14, 86%, 42%);
}
.tienda .precio-unidad {
  margin-right: 5px;
}
.tienda .eliminar {
  width: 15px;
  height: 15px;
  border-radius: 50px;
  display: grid;
  place-content: center;
  border: 1px solid gray;
  background-color: white;
}
.tienda .eliminar:hover {
  cursor: pointer;
  border: 1px solid;
}
.tienda .eliminar:hover img {
  filter: brightness(0);
}
.tienda .total-orden {
  width: 100%;
  align-items: center;
}
.tienda .total-orden p {
  margin-top: 10px;
  margin-bottom: 30px;
}
.tienda .precio-final {
  font-weight: 700;
  font-size: 25px;
}
.tienda .ecologico {
  background-color: hsl(13, 31%, 94%);
  width: 100%;
  justify-content: center;
  gap: 10px;
}
.tienda .ecologico p {
  color: black;
}
.tienda .confirmar-orden {
  margin-top: 25px;
  margin-bottom: 25px;
  width: 100%;
  color: white;
}
.tienda .confirmar-orden:hover {
  color: white;
  background-color: hsl(14, 86%, 35%);
  border: 1px solid hsl(14, 86%, 35%);
}
.tienda .orden-confirmada {
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  background-color: hsla(17, 0%, 0%, 0.5);
}
.tienda .resumen-orden {
  max-width: 590px;
  max-height: 100%;
  width: 100%;
  background-color: white;
  border-radius: 10px;
  overflow: auto;
}
.tienda .cabecera-resumen {
  margin-top: 40px;
}
.tienda .cabecera-resumen h2 {
  margin-bottom: 0;
  font-size: 35px;
  margin-top: 20px;
}
.tienda .cabecera-resumen p {
  margin-bottom: 20px;
  margin-top: 0;
}
.tienda .resumen-precio {
  background-color: hsl(20, 50%, 98%);
}
.tienda .resumen-precio, .tienda .resumen-precio img {
  border-radius: 5px;
}
.tienda .resumen-orden button, .tienda .resumen-orden .resumen-precio, .tienda .cabecera-resumen {
  margin-right: 6.75%;
  margin-left: 6.75%;
  width: 86.5%;
}
.tienda .lista-confirmada {
  padding-top: 25px;
}
.tienda .lista-confirmada .articulo-carrito .precio-total {
  margin-top: 0;
  color: black;
}
.tienda .lista-confirmada, .tienda .total-orden {
  margin-right: 5.6%;
  margin-left: 5.6%;
}
.tienda .lista-confirmada .articulo-carrito, .tienda .total-orden {
  width: 88.8%;
}
.tienda .lista-confirmada .articulo-carrito {
  margin-bottom: 15px;
}
.tienda .info {
  gap: 15px;
}
.tienda .info img {
  width: 50px;
  height: 50px;
}
.tienda .datos-articulo p {
  margin-bottom: 15px;
}
.tienda .ocultar {
  display: none;
}
.tienda .articulo-seleccionado {
  outline: 3px solid hsl(14, 86%, 42%);
}