.usuario {
  margin: 0 10% 0 10%;
}
.usuario__contenedor {
  display: flex;
}
.usuario__navegacion {
  margin-right: 5rem;
}
.usuario__navegacion ul {
  list-style: none;
  padding: 0;
}
.usuario .datos__tipo {
  font-weight: 600;
  text-transform: uppercase;
}
.usuario__acciones {
  margin-bottom: 3rem;
}
.usuario .formulario__contenedor {
  display: flex;
}
.usuario .formulario__cerrar {
  height: 30px;
  width: 30px;
  border-radius: 50px;
}
.usuario .formulario__cerrar:hover {
  cursor: pointer;
}
.usuario .formulario--ocultar {
  display: none;
}