.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(7, 20%, 60%);
  height: 8rem;
  width: 89%;
  padding-left: 5.5%;
  padding-right: 5.5%;
  margin-bottom: 50px;
}
.header__logo {
  font-size: 3rem;
  color: hsl(14, 65%, 9%);
  font-weight: 700;
}
.header__logo:hover {
  font-size: 3.1rem;
}
.header__navegacion {
  width: 50%;
}
.header__navegacion ul {
  display: flex;
  justify-content: end;
  list-style: none;
  gap: 2rem;
  padding: 0;
  width: 100%;
}
.header__enlace, .header__submit {
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
}
.header__enlace:hover, .header__submit:hover {
  cursor: pointer;
  color: hsl(14, 86%, 42%);
}
.header__submit {
  background-color: transparent;
  border: none;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(7, 20%, 60%);
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 70px;
}

.login,
.registro {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.formulario {
  min-width: 325px;
  width: 30%;
  margin-bottom: 20px;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.formulario__label {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-size: 1.25rem;
}
.formulario__input {
  border-radius: 0.25rem;
  height: 3rem;
  border: 1px solid hsl(7, 20%, 60%);
  padding-left: 1rem;
}
.formulario__input::placeholder {
  color: hsl(14, 25%, 72%);
  font-size: 1.2rem;
}
.formulario__submit {
  height: 2.5rem;
  width: 10rem;
  text-transform: uppercase;
  color: white;
  font-weight: 600;
  background-color: hsl(14, 86%, 42%);
  border-radius: 50px;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: hsl(14, 86%, 32%);
}

.acciones {
  display: flex;
  min-width: 325px;
  width: 30%;
  justify-content: space-between;
  gap: 5px;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: hsl(14, 86%, 42%);
}
.acciones__enlace:hover {
  color: hsl(14, 65%, 9%);
}

.alerta {
  box-sizing: border-box;
  min-width: 325px;
  width: 30%;
  margin-bottom: 20px;
  padding: 10px 1rem;
  color: white;
  font-weight: 600;
  font-size: 17px;
  text-transform: uppercase;
  border-radius: 7px;
}
.alerta__error {
  background-color: rgb(169, 0, 0);
}
.alerta__exito {
  background-color: rgb(1.5384615385, 168.4615384615, 1.5384615385);
}